import React from "react";
import PropTypes from 'prop-types';
import MomentUtils from '@date-io/moment';

//Material UI Components
import TextField from '@material-ui/core/TextField';
import { MuiPickersUtilsProvider, KeyboardDatePicker } from '@material-ui/pickers';
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';

//Styles
import useStyles from './newbooking-styles';


export default function BookingForm(props) {
  const {
    submitBooking,
    selectedDate,
    handleDateChange,
    offices,
    levels,
    selectedOffice,
    changeOffice,
    selectedLevel,
    changeLevel
  }=props;
  const classes = useStyles();

  return (
    <div>
      <form>
        <p><TextField id="outlined-basic" label="Name" variant="outlined" className={classes.formItem} size="small"/></p>
        <p>
          <MuiPickersUtilsProvider utils={MomentUtils}>
            <KeyboardDatePicker
              variant="inline"
              format="DD/MM/yyyy"
              margin="normal"
              id="date"
              label="Date"
              value={selectedDate}
              onChange={handleDateChange}
              KeyboardButtonProps={{'aria-label': 'change date'}}
              className={classes.formItem}
            />
          </MuiPickersUtilsProvider>
        </p>
        <p>
          <Select
            labelId="officeLocation"
            id="officeLocation"
            value={selectedOffice}
            label="Office Location"
            className={classes.formItem}
            onChange={(e) => changeOffice(e)}
          >
            {offices.map((office, key) => {return(
              <MenuItem key={key} value={office.name}>{office.name}</MenuItem>)
            })}
          </Select>
        </p>
        <p>
          <Select
            labelId="level"
            id="level"
            label="Level"
            className={classes.formItem}
            value={selectedLevel}
            onChange={(e) => changeLevel(e)}
          >
            {levels.map((e, key) => {return(
              <MenuItem key={key} value={e}>{e}</MenuItem>)
            })}
          </Select>
        </p>
        <p><Button onClick={() => submitBooking()} className={classes.formItem}><Typography variant="subtitle1">Book</Typography></Button></p>
      </form>
    </div>
  );
}

BookingForm.propTypes = {
  submitBooking: PropTypes.func,
  selectedDate: PropTypes.instanceOf(Date),
  handleDateChange: PropTypes.func,
  offices: PropTypes.object,
  levels: PropTypes.object,
  selectedOffice: PropTypes.String,
  selectedLevel: PropTypes.String,
  changeOffice: PropTypes.func,
  changeLevel: PropTypes.func
}