import React from "react";
import PropTypes from 'prop-types';

//Material UI Components
import Typography from '@material-ui/core/Typography';

//Styles
import useStyles from './newbooking-styles';

export default function BookingMap(props) {
  const {
    selectedLevel
  }=props;
  const classes = useStyles();

  return (
    <div>
      <iframe
        className={classes.map}
        frameBorder="0" 
        scrolling="no" marginHeight="0" marginWidth="0" id="gmap_canvas" 
        src="https://maps.google.com/maps?width=800&amp;height=800&amp;hl=en&amp;q=%20Birmingham+()&amp;t=&amp;z=12&amp;ie=UTF8&amp;iwloc=B&amp;output=embed">
      </iframe>
      <script type='text/javascript' src='https://embedmaps.com/google-maps-authorization/script.js?id=3b974e1d527760e8e5c79095b649ce63e7752144'></script>
      <Typography variant="h6" className={classes.deskSelected} >Desk Selected: {selectedLevel  ? selectedLevel + '.01' : 'N/A'}</Typography>
    </div>
  );
}

BookingMap.propTypes = {
  selectedLevel: PropTypes.string
}