import React from "react";

//Project Components
import Nav from '../../components/nav';
import BookingSuccess from './bookingSuccess.jsx';
import BookingForm from './bookingForm.jsx';
import BookingMap from './bookingMap.jsx';

//Material UI Components
import Grid from '@material-ui/core/Grid';

//Hooks
import useHooks from './hooks/newbooking-hooks.js';

export default function NewBookings() {
  const {
    bookingComplete,
    submitBooking,
    selectedDate,
    handleDateChange,
    bookingSuccessData,
    offices,
    levels,
    selectedOffice,
    selectedLevel,
    changeLevel,
    changeOffice
  }=useHooks();

  return (
    <div>
      <Nav item={2} />
      {bookingComplete ?
        <BookingSuccess 
          bookingSuccessData={bookingSuccessData}
        />
      :
        <Grid container>
          <Grid item xs={1} />
          <Grid item xs={5}>
            <BookingForm 
              submitBooking={() => submitBooking()}
              selectedDate={selectedDate}
              handleDateChange={handleDateChange}
              offices={offices}
              levels={levels}
              selectedOffice={selectedOffice}
              changeOffice={changeOffice}
              selectedLevel={selectedLevel}
              changeLevel={changeLevel}
            />
          </Grid>
          <Grid item xs={5}>
            <BookingMap 
              selectedLevel={selectedLevel}
            />
          </Grid>
          <Grid item xs={1} />
        </Grid>
      }
    </div>
  );
}
