import React from "react";

//Material UI Components
import Grid from '@material-ui/core/Grid';
import CheckIcon from '@material-ui/icons/Check';
import Box from "@material-ui/core/Box";


//Styles
import useStyles from './newbooking-styles';

export default function BookingSuccess() {
  const classes = useStyles();
  const bookingSuccessData = {
    name: "Miles Jackson",
    date: "11/05/2021",
    office: "Midlands Campus",
    level: "B2",
    desk: "14"
  };

  return (
    <div>
      <Grid container>
        <Grid item xs={4} />
        <Grid item xs={4}>
          <Box
            display="flex"
            justifyContent="center"
            alignItems="center"
          >
            <CheckIcon className={classes.check} />
          </Box>
          <Box
            display="flex"
            justifyContent="center"
            alignItems="center"
          >
            <h3>Booking Complete</h3>
          </Box>
          <p>Name: {bookingSuccessData.name}</p>
          <p>Date: {bookingSuccessData.date}</p>
          <p>Office Location: {bookingSuccessData.office}</p>
          <p>Level: {bookingSuccessData.level}</p>
          <p>Desk: {bookingSuccessData.desk}</p>
        </Grid>
        <Grid item xs={4} />
      </Grid>
    </div>
  );
}