import React from "react";
//import AppBar from "@material-ui/core/AppBar";
import Tabs from "@material-ui/core/Tabs";
import LinkTab from "@material-ui/core/Tab";
import EventIcon from "@material-ui/icons/Event";
import AddCircle from "@material-ui/icons/AddCircle";
import PermContactCalendar from "@material-ui/icons/PermContactCalendar";
import Paper from "@material-ui/core/Paper";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles({
  root: {
    flexGrow: 1,
  },
});

function Nav(props) {
  const classes = useStyles();
  const {
    // eslint-disable-next-line react/prop-types
    item,
  } = props;

  console.log("item", item);

  return (
    <Paper className={classes.root}>
      <Tabs variant="fullWidth" value={item} aria-label="Menu" centered>
        <LinkTab label="Calendar" icon={<EventIcon />} href="/calendar" />
        <LinkTab
          label="My Bookings"
          icon={<PermContactCalendar />}
          href="/mybookings"
        />
        <LinkTab label="New Booking" icon={<AddCircle />} href="/newbooking" />
      </Tabs>
    </Paper>
  );
}

export default Nav;
